// src/lib/supabaseClient.js
import { createClient } from "@supabase/supabase-js";

//   Vite  CRA 
const supabaseUrl =
  import.meta.env.VITE_SUPABASE_URL || process.env.REACT_APP_SUPABASE_URL;
const supabaseAnonKey =
  import.meta.env.VITE_SUPABASE_ANON_KEY || process.env.REACT_APP_SUPABASE_ANON_KEY;

//  
if (!supabaseUrl || !supabaseAnonKey) {
  console.error(" Missing Supabase environment variables.");
} else {
  console.log(" Supabase URL check:", supabaseUrl);
}

/**
 *   Supabase 
 * -  GoTrueClient 
 * - 
 */
if (!window.__supabaseClient__) {
  console.log(" Initializing Supabase Client once");
  window.__supabaseClient__ = createClient(supabaseUrl, supabaseAnonKey);
}

export const supabase = window.__supabaseClient__;
